.colors-menu {
   .colors-list {
      display: flex; width: 100px; flex-wrap: wrap; justify-content: space-around;
      .color {
         width: 20px; height: 20px; margin: 5px;
         cursor: pointer;
         opacity: .8;
         border-radius: 50%;
         -webkit-border-radius: 50%;
         -moz-border-radius: 50%;
         -ms-border-radius: 50%;
         -o-border-radius: 50%;
         &:hover {
            opacity: 1;
         }
      }
   }
}