.overflow-hidden {overflow: hidden;}

.modal-wrapper {
   position: fixed; top: 0; left: 0; width: 100%; height: 100%; z-index: 1200;
   background-color: rgba(128, 128, 128, 0.322);
   overflow: auto;
   .modal-close {
      padding: 5px 10px; width: 50px;
      cursor: pointer; 
   }
   .modal-body {
      background-color: #ffffff;
      width: 90%; margin: auto;
      
      .modal-header {
         padding: 10px; 
         display: flex; justify-content: space-between;
         .photographer a {
            display: flex; align-items: center; 
            color: black; text-decoration: none;
            img {
               margin-right: 5px;
               border-radius: 50%;
               -webkit-border-radius: 50%;
               -moz-border-radius: 50%;
               -ms-border-radius: 50%;
               -o-border-radius: 50%;
            }
         }
         .download a {
            color: black; text-decoration:none;
         }
      }
      .modal-image {
         height: 500px; 
         display: flex;
         align-items: center;
         img {
            margin: auto; display: block; max-height: 100%; max-width: 50%;
            
         } 
      }
      .image-description {margin-bottom: 30px; text-align: center; font-style: italic;}
   }
}