.banner {
   position: relative;
   background-size: cover;
   height: 700px;
   .overlay {
      position: absolute;
      top: 0; left: 0;
      width: 100%; height: 100%;
      background-image: linear-gradient(#000000a8, #ffffff00);
   }
   .searchBox {
      position: relative;
      top: 50%; margin: auto; width: 40%;
      height: 40px;
      transform: translateY(-50%);
      -webkit-transform: translateY(-50%);
      -moz-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      -o-transform: translateY(-50%);
}
}